@import url("https://fonts.googleapis.com/css2?family=Noto+Sans:wght@400;700&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.container-fluid {
  padding: 0;
}
.navbar {
  background-color: rgb(26, 159, 255);
}
.navbar .container {
  align-items: flex-start;
  position: relative;
}
.navbar-brand {
  position: absolute;
  left: -50px;
  display: flex;
  z-index: 9;
}
.brandImg {
  position: relative;
  width: 200px;
  height: 120px;
}
.brandImg img {
  position: absolute;
}
.brandImg img:first-child {
  width: 230%;
  z-index: -1;
  top: -10px;
  left: -30px;
}
.brandImg img:last-child {
  left: 130px;
}
.brandName {
  position: absolute;
  top: 60px;
  left: 350px;
  display: flex;
  align-items: center;
}
.navbar-collapse {
  padding-left: 17%;
}
.navbar-nav{
  z-index: 9;
}
.phone {
  display: flex;
  align-items: center;
  margin-left: 20rem;
}
.phone h3 {
  border-left: 1px solid rgb(104, 104, 104);
}
.phone .fa-phone-alt {
  margin-right: 10px;
  transform: rotate(90deg);
}
.nav-link {
  font-family: "Noto Sans", sans-serif;
  font-weight: 900;
}
.nav-link:hover {
  color: rgb(255, 255, 255) !important;
}
header,
.rahbariyat {
  margin-top: 10rem;
  font-family: "Noto Sans", sans-serif;
}
header .headerItem li {
  margin: 20px 0;
}
header .headerItem li:hover p {
  color: rgb(26, 159, 255);
}
header .headerItem li a {
  text-decoration: none;
  display: flex;
  align-items: center;
}
header .headerItem li a p {
  margin: 0;
  margin-left: 15px;
  color: #000;
  transition: 0.3s color;
}
.headerTitle a {
  text-decoration: none;
  font-size: 16px;
  color: rgb(26, 159, 255);
  margin-left: 80px;
}
.headerTitle a .fa-angle-right {
  font-size: 13px;
  margin-left: 4px;
  color: #000;
}
