.headerTitle {
  font-weight: 600;
}

.headerTitle a:hover {
  color: #000;
}

.headerTitle .dots {
  margin-left: 40px;
}

.headerTitle .dots i {
  font-size: 14px;
  margin: 0 5px;
  cursor: pointer;
}

.headerTitle .dots i:first-child {
  font-size: 16px;
  color: #1a9fff;
}

.sdfrtg .lists {
  margin: 0;
  border-bottom: 1px solid gray;
  padding: 10px 0;
  cursor: -webkit-grab;
  cursor: grab;
}

.sdfrtg .lists .listsLink {
  color: #1a9fff;
  font-weight: 600;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
  text-decoration: none;
}

.sdfrtg .lists .listsLink:hover {
  color: #000;
}

main .headerTitle {
  font-family: "Noto Sans", sans-serif;
}

main .headerTitle .fa-grip-lines-vertical {
  margin: 0 10px;
  color: black;
}

main .sliderItems .sliderItem img {
  width: 100%;
}

main .sliderItems .sliderItem .cardTitle .titleDate {
  font-weight: 700;
}

main .sliderItems .sliderItem .cardText a {
  text-decoration: none;
  color: #000;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
  font-size: 24px;
}

main .sliderItems .sliderItem .cardText a:hover {
  color: #1a9fff;
}

main .news .list a {
  text-decoration: none;
  color: #000;
  font-family: "Noto Sans", sans-serif;
}

main .news .list a:hover {
  color: #1a9fff;
}

footer .refresh p,
footer .workDay p {
  font-family: "Noto Sans", sans-serif;
  font-size: 13px;
}

footer .linkOne li a {
  text-decoration: none;
  color: #000;
  font-family: "Noto Sans", sans-serif;
  font-size: 14px;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
}

footer .linkOne li a:hover {
  color: white;
}

footer .linkOne li a i {
  margin-right: 5px;
}

footer .workDay .social a {
  color: #000;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
}

footer .workDay .social a:hover {
  color: white;
}

footer .workDay .social a i {
  font-size: 24px;
  margin-right: 20px;
}

::-webkit-scrollbar {
  width: 0;
}

::-moz-selection {
  background: #e6e6e6;
}

::selection {
  background: #e6e6e6;
}

.rahbariyat {
  margin-bottom: 4rem;
}

.rahbariyat .sliderOffice img {
  width: 100%;
  height: 100%;
}

.rahbariyat .officeText {
  font-family: "Noto Sans", sans-serif;
}

.rahbariyat .officeText .text {
  text-align: justify;
}

.rahbariyat .card .bossImg img {
  width: 100%;
  height: 100%;
}

.rahbariyat .card .faoliyat {
  padding: 0 12px;
}

.rahbariyat .card .faoliyat div {
  background-color: #e7f1ff;
  padding: 10px;
}

.rahbariyat .card .faoliyat div p {
  margin: 0;
}

.doc {
  height: 100vh;
}

.btn:focus,
.btn,
a:focus {
  outline: none;
  -webkit-box-shadow: none;
          box-shadow: none;
}

.navbar-toggler:focus,
.accordion-button:focus {
  outline: none !important;
  -webkit-box-shadow: none;
          box-shadow: none;
}

.caption {
  margin-bottom: 20px;
}

.caption i {
  color: #1a9fff;
  margin-right: 5px;
}

.caption span {
  color: gray;
  font-size: 15px;
}

.caption:last-child {
  margin: 0;
}

.caption .bossAllInfo {
  cursor: pointer;
}

.caption .bossAllInfo span {
  border-bottom: 2px solid transparent;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

.caption .bossAllInfo span:hover {
  border-bottom: 2px solid #1a9fff;
  color: #1a9fff;
}

.dropdown-item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding-right: 0;
}

.dropdown-item img {
  width: 20px;
  margin-left: 10px;
}

.brn {
  display: none;
}

@media (max-width: 575.98px) {
  .brandImg img:nth-child(1) {
    top: -50px;
    opacity: 0.5;
  }
  .brandImg img:nth-child(2) {
    display: none;
  }
  .navbar-collapse {
    padding: 0;
  }
  .navbar-nav {
    max-height: 100% !important;
  }
  header {
    margin-top: 10px !important;
  }
  .navbar > .container {
    -webkit-box-pack: end;
        -ms-flex-pack: end;
            justify-content: flex-end;
  }
  .navbar-brand {
    z-index: -99;
  }
  .collapsing {
    height: 100% !important;
  }
  .brn {
    display: block;
    position: absolute;
    top: -5px;
    left: 0;
  }
  .brn .name h6 {
    color: #000;
  }
  .sdfrtg .row .col-md-6:nth-child(2) {
    display: none;
  }
  .brandName {
    display: none !important;
  }
  .news .py-2 ul {
    padding: 0 !important;
  }
  .allinfo {
    padding-top: 4rem;
  }
  .rahbariyat {
    margin-top: 4rem;
  }
  .brandImg {
    display: none;
  }
  .row {
    --bs-gutter-x: 0;
  }
  .card {
    width: 100% !important;
  }
}

.slick-arrow {
  display: none !important;
}

.slick-prev {
  left: 0 !important;
  -webkit-transform: translate(0, 480%);
          transform: translate(0, 480%);
}

.slick-prev:before {
  font-size: 20px;
  line-height: 1;
  opacity: 0.75;
  color: black !important;
}

.slick-dots {
  bottom: -50px !important;
}

.press {
  color: #1a9fff;
  cursor: pointer;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
}

.press:hover {
  color: #ff6500;
}

.pressedLink {
  color: #ff6500;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
}

.pressedLink:hover {
  color: #1a9fff;
}
